ion{min-height:350px;padding:0 10px}.presentation::before{font-size:calc(1.2rem + 2vw);line-height:1.5;margin-bottom:2.5vh;max-width:95%}.presentation::after{font-size:calc(.7rem + 1.2vw);line-height:1.7;max-width:90%}}@media(max-width: 360px){.presentation{min-height:300px}.presentation::before{font-size:calc(1rem + 2vw);line-height:1.55}.presentation::after{font-size:calc(.6rem + 1.2vw);line-height:1.75}}